* {
  box-sizing: border-box;
}

html,
/*body,*/
#root,
#root > div,
div[data-reactroot] {
  height: 100%;
  padding: 0;
  margin: 0;
}



/* nab bar edits to bootstrap css*/
.navbar-brand.nav-brand-custom {
  border-radius: 5px;
  background-color: #6b9ac4;
  color: #eff2f1;
  font-size: 20px;
  padding: 10px;
}

