/* styling classes*/
.underline {
  border-bottom: 2px solid #97d8c4;
}

.custom-img-500 {
  width: 50%;
  height: auto;
  padding: 20px;
}

/* style to define boxes containing text*/
.text-box {
  border-radius: 10px;
  background-color: #eff2f1;
  padding-top: 25px;
  padding-bottom: 25px;
}

.text-box h1 {
  color: #4059ad;
}

.text-box p {
 padding:10px;
}

form .btn-primary {
  background-color: #6b9ac3; 
  border-color: #6b9ac3;
  font: #eff2f1;
}

.footer {
  height:50px;
  margin-top: -50px;
  position: fixed;
  left: 0;
  bottom: 0;
  background-color: #6b9ac4;
}

.footer p {
color: #eff2f1;
}